import React, { Component } from 'react';
import Flippy, { FrontSide, BackSide } from 'react-flippy';
import { skillsData } from '../data/Skills';

class Skills extends Component {
  render() {
    return (
      <section id='skills'>
        <h1>Top Skills/Technologies</h1>
        <div className='skills__container'>
          {skillsData &&
            skillsData.map((item) => (
              <Flippy
                flipOnHover={true}
                flipDirection='horizontal'
                className='skills__card'
              >
                <FrontSide
                  style={{
                    backgroundColor: '#D3D3D3',
                    borderTop: `5px solid ${item.logoColor}`,
                  }}
                  className='skills__card__side'
                >
                  <h2 style={{ color: '#000', marginBottom: 30 }}>
                    {item.title}
                  </h2>
                  <img
                    src={item.image}
                    alt={item.title}
                    width={200}
                    height={200}
                  />
                </FrontSide>
                <BackSide
                  style={{ backgroundColor: '#D3D3D3' }}
                  className='skills__card__side'
                >
                  <p style={{ fontSize: 20, color: '#000' }}>
                    {item.description}
                  </p>
                </BackSide>
              </Flippy>
            ))}
        </div>
      </section>
    );
  }
}

export default Skills;
