import React, { Component } from 'react';
import {
  VerticalTimeline,
  VerticalTimelineElement,
} from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import { timelineData } from '../data/Timeline';

class Timeline extends Component {
  render() {
    return (
      <section id='timeline'>
        <VerticalTimeline>
          {timelineData &&
            timelineData.map((item) => (
              <VerticalTimelineElement
                className='vertical-timeline-element'
                contentStyle={{
                  background: item.backgroundColor,
                  color: item.textColor,
                  borderTop: `5px solid ${item.iconColor}`,
                }}
                contentArrowStyle={{
                  borderRight: item.arrowRight
                    ? '7px solid  rgb(33, 150, 243)'
                    : null,
                  borderLeft: item.arrowLeft
                    ? '7px solid  rgb(33, 150, 243)'
                    : null,
                }}
                date={item.date}
                dateClassName='vertical-timeline-element__dates'
                iconStyle={{
                  background: item.iconBackgroundColor || '#000',
                  color: item.iconColor || '#fff',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                }}
                icon={item.icon}
              >
                <h3>{item.title}</h3>{' '}
                {
                  <div
                    style={{
                      display: 'flex',
                      marginBottom: '15px',
                      flexWrap: 'wrap',
                    }}
                  >
                    {' '}
                    {item.skills &&
                      item.skills.map((skill) => (
                        <div
                          style={{
                            backgroundColor: item.skillColor || '#000',
                            borderRadius: '0.5em',
                            padding: '0px 20px',
                            marginRight: '10px',
                            color: item.skillTextColor || '#FFF',
                            marginBottom: '10px',
                          }}
                        >
                          {' '}
                          {skill}
                        </div>
                      ))}
                  </div>
                }
                <p>
                  {item.description.map((point) => (
                    <li>{point}</li>
                  ))}
                </p>
              </VerticalTimelineElement>
            ))}
        </VerticalTimeline>
      </section>
    );
  }
}

export default Timeline;
